.form {
  /* text-align: right; */
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form button {
  font: inherit;
  cursor: pointer;
  background-color: green;
  border: 1px solid green;
  color: white;
  padding: 0.25rem 1rem;
  font-weight: bold;
  border-radius: 0.2em;
}

.form button:hover,
.form button:active {
  background-color: rgb(0, 161, 0);
  border-color: rgb(0, 161, 0);
}
